ul li code,
ol li code {
  background-color: rgba(216, 71, 39, 0.1);
  border-radius: 3px;
  font-family: "Fira Mono", monospace;
  padding: 0.2em 0.4em;
  font-size: 0.9em;
  color: #d84727; /* orangeValencia */
}

/* Dark mode support for inline code */
.dark ul li code,
.dark ol li code {
  background-color: rgba(249, 125, 110, 0.1);
  color: #f97d6e; /* orangeValencia-400 */
}

/* Center image captions and adjust spacing */
figure {
  margin: 1.5em auto; /* Auto left/right margins for horizontal centering */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.kg-card-hascaption figcaption {
  text-align: center;
  margin-top: 0.5em;
  font-size: 0.75em;
  color: #666;
}

.dark figcaption {
  color: #a0a0a0;
}

/* Fix for all inline code */
:not(pre) > code {
  background-color: rgba(216, 71, 39, 0.1);
  border-radius: 3px;
  font-family: "Fira Mono", monospace;
  padding: 0.2em 0.4em;
  font-size: 0.9em;
  color: #d84727; /* orangeValencia */
}

.dark :not(pre) > code {
  background-color: rgba(249, 125, 110, 0.1);
  color: #f97d6e; /* orangeValencia-400 */
}
